html, body {
  width: 100%;
  height: 100%;
}

body {
  padding: 0;
  margin: 0;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

div, p, a, span, em, strong, img, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, td, tr, form, fieldset, label, select, input, textarea, header, nav, section, article, aside, footer, blockquote, figure {
  padding: 0;
  margin: 0;
  outline: none;
  list-style: none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

header, nav, section, article, aside, footer {
  display: block;
}

select, input, textarea {
  font: inherit;
  color: inherit;
  vertical-align: middle;
}

textarea {
  vertical-align: top;
  resize: none;
}

:focus {
  outline: none;
}

input[type=submit], button {
  cursor: pointer;
}

img, fieldset {
  border: 0;
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  font: inherit;
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

body {
  font: 400 24px "Montserrat", sans-serif;
  height: 100%;
  color: #383838;
  letter-spacing: 0.02em;
  background-color: #ffffff;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
  padding-top: 114px;
}

.bg-grey {
  background-color: #FAFAFA;
}

.color_green {
  color: #7EA68C;
}

sup {
  font-size: inherit;
  vertical-align: text-bottom;
}

section {
  padding: 50px 0;
  margin: 50px 0;
}

.section__title {
  margin-bottom: 50px;
  text-transform: uppercase;
  text-align: center;
}
.section__content p {
  margin-bottom: 1.75em;
}
.section__content p:last-child {
  margin-bottom: 0;
}

.d-flex {
  display: flex;
}

.main-banner {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  width: 100%;
}
.main-banner img {
  width: 100%;
  height: auto;
}

.dynamic-content p {
  margin-bottom: 15px;
  line-height: 1.1;
}
.dynamic-content img {
  max-width: 100%;
  height: auto;
}
.dynamic-content img.alignleft {
  float: left;
  margin: 4px 25px 25px 0;
}
.dynamic-content img.alignright {
  float: right;
  margin: 4px 0 25px 25px;
}
.dynamic-content img.aligncenter {
  display: block;
  margin: 50px auto;
  clear: both;
}
.dynamic-content img.alignfull {
  width: 100%;
  height: auto;
  display: block;
  margin: 50px 0;
}
.dynamic-content h1, .dynamic-content h2,
.dynamic-content h3, .dynamic-content h4, .dynamic-content h5, .dynamic-content h6 {
  line-height: 1.1;
  text-transform: uppercase;
  color: #7EA68C;
  text-align: center;
}
.dynamic-content h3, .dynamic-content h4, .dynamic-content h5, .dynamic-content h6 {
  font-weight: 700;
}
.dynamic-content h1, .dynamic-content h2 {
  font-size: 52px;
  font-weight: 400;
}
.dynamic-content h3 {
  font-size: 36px;
  margin: 50px 0;
}
.dynamic-content h4 {
  font-size: 26px;
}
.dynamic-content ul, .dynamic-content ol {
  margin-bottom: 20px;
}
.dynamic-content ol {
  counter-reset: list;
}
.dynamic-content ol li {
  overflow-wrap: anywhere;
  position: relative;
  list-style: inherit;
  padding-left: 35px;
}
.dynamic-content ol li:before {
  position: absolute;
  top: 0;
  left: 8px;
  counter-increment: list;
  content: counter(list) ".";
}
.dynamic-content ul li:before {
  content: ".";
  display: block;
  position: absolute;
  left: 17px;
  top: -8px;
}
.dynamic-content li {
  position: relative;
  padding-left: 34px;
  margin-bottom: 5px;
}
.dynamic-content a {
  text-decoration: underline;
  overflow-wrap: anywhere;
}
.dynamic-content a:hover, .dynamic-content a:focus {
  text-decoration: none;
}

.article .big-marker-ul li {
  margin-bottom: 25px;
}
.article .big-marker-ul li:before {
  content: "";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #7EA68C;
  left: 0;
  top: 7px;
}

@media only screen and (max-width: 1439px) {
  body {
    font-size: 20px;
  }
  .dynamic-content h1, .dynamic-content h2 {
    font-size: 48px;
  }
  .dynamic-content h3 {
    font-size: 34px;
  }
}
@media only screen and (max-width:1024px) {
  .main {
    padding-top: 100px;
  }
}
@media only screen and (max-width: 991px) {
