  }
}
@media only screen and (max-width: 767px) {
  body {
    font-size: clamp(16px, 16px + 4 * (100vw - 580px) / 187, 20px);
  }
}
@media only screen and (max-width: 580px) {
  .section__title {
    margin-bottom: 25px;
  }
  .dynamic-content img.alignfull {
    margin: 25px 0;
  }
