.btn {
  display: inline-block;
  cursor: pointer;
  text-align: center;
  transition: 0.35s;
  font-weight: 800;
  font-size: 18px;
  line-height: 1;
  font-family: "Montserrat", sans-serif;
  padding-top: 19px;
  padding-bottom: 19px;
}
.btn img {
  width: 100%;
  max-width: 100%;
  display: inline-block;
}
.btn_main {
  background-color: #7EA68C;
  color: #ffffff;
  padding-left: 44px;
  padding-right: 44px;
  border: none;
}
.btn_outline {
  background-color: transparent;
