.menu {
  display: flex;
  justify-content: center;
  width: 100%;
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
  letter-spacing: 0;
  align-items: flex-end;
}
.menu__item {
  padding: 5px 31px 5px 0;
  margin-left: 31px;
  position: relative;
}
.menu__item:after {
  position: absolute;
  content: "/";
  display: block;
  line-height: 1.5;
  color: #383838;
  bottom: 0;
  right: 0;
  transform: translateX(50%);
}
.menu__item:last-child:after {
  display: none;
}
.menu__item:hover .menu__sub-menu {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
  transition: transform 0.4s, opacity 0.3s;
}
.menu__link {
  position: relative;
  display: inline-block;
  color: inherit;
  transition: 0.35s;
}
.menu__link:after {
  position: absolute;
  content: "";
  display: block;
  background-color: #7EA68C;
  width: 0;
  height: 2px;
  bottom: -5px;
  transition: width 0.35s;
}
.menu__link.active:after, .menu__link:hover:after {
  width: 100%;
}
.menu__toggler {
  display: none;
}
.menu__sub-menu {
  position: fixed;
  left: 0;
  right: 0;
  opacity: 0;
  background-color: #fff;
  -webkit-transform: translateY(25px);
  transform: translateY(25px);
  pointer-events: none;
  transition: opacity 0.35s ease;
  width: 100vw;
  top: 87px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.15);
  z-index: 99;
}
.menu__sub-menu li {
  margin-bottom: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #E9EEF2;
  color: #6E8396;
}
.menu__sub-menu li:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none;
}
.menu__sub-menu li:hover {
  color: #383838;
}
.menu__sub-title {
  color: #7EA68C;
  text-align: center;
  transition: 0.35s;
  display: inline-block;
  margin-bottom: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #E9EEF2;
}
.menu__sub-title:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none;
}
.menu__sub-title:hover {
  color: #007934;
}

.nav-row {
  display: flex;
  height: auto;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  z-index: 300;
  padding: 50px 64px 25px;
}

.nav-col {
  width: 20%;
  padding: 0 25px;
  border-left: 1px solid #E9EEF2;
}
.nav-col:first-child {
  border-left: none;
}

@media only screen and (max-width: 1280px) {
  .menu {
    font-size: 17px;
  }
  .menu__item {
    padding-right: 20px;
    margin-left: 20px;
  }
}
@media only screen and (max-width: 991px) {
  .menu {
    display: block;
    height: 100%;
    max-height: 100vh;
    overflow: scroll;
  }
  .menu__item {
    position: relative;
  }
  .menu__item:after {
    display: none;
  }
  .menu__link {
    padding-bottom: 10px;
    padding-top: 10px;
  }
  .menu__sub-menu li {
    border: none;
    margin-bottom: 10px;
    padding-bottom: 5px;
    padding-left: 20px;
  }
  .menu__sub-title {
    border: none;
    margin-bottom: 5px;
    padding-bottom: 0;
    padding-left: 20px;
    text-align: left;
  }
  .menu__item > .menu__sub-menu {
    position: static;
    max-height: 0;
    overflow: hidden;
    opacity: 1;
    pointer-events: all;
    transform: none;
    transition: max-height 0.35s ease;
    box-shadow: none;
    width: 100%;
  }
  .nav-col.is-open > .menu__sub-menu,
  .menu__item.is-open > .menu__sub-menu {
    max-height: 3000px;
    margin-top: 15px;
  }
  .menu__item:hover > .menu__sub-menu {
    opacity: 1;
    transform: none;
    pointer-events: all;
  }
  .nav-row {
    flex-direction: column;
    padding: 0;
  }
  .nav-col {
    position: relative;
    width: 100%;
    padding: 0 30px 0 0;
    border: none;
    margin-bottom: 15px;
  }
  .nav-col > .sub-menu {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.35s ease;
    margin-top: 0;
    padding-left: 15px;
  }
  .nav-col.is-open > .sub-menu {
    max-height: 2000px;
    margin-top: 10px;
  }
  .menu__toggler {
    display: block;
    position: absolute;
    right: 0;
    top: 10px;
    width: 27px;
    height: 27px;
    background-image: url(../assets/img/arrow-down.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: transparent;
    border: none;
    transition: transform 0.3s;
    z-index: 1;
  }
  .nav-col .menu__toggler {
    top: -5px;
  }
  .menu__item.is-open > .menu__toggler {
    transform: rotate(90deg);
  }
  .nav-col.is-open > .menu__toggler {
    transform: rotate(90deg);
  }
}
@media only screen and (max-width: 580px) {
  .header__nav {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.swiper-pagination.swiper-pagination-bullets {
  display: none;
  justify-content: center;
  gap: 3px;
  bottom: 0;
}

.swiper-pagination-bullet {
  width: 50px;
  height: 2px;
  background-color: #E4E4E4;
  border-radius: 2px;
  opacity: 1;
  transition: background-color 0.3s ease;
}

.swiper-pagination-bullet-active {
